<template>
	<view class="userLayout pageBg" v-if="userInfo">
		<view :style="{height: getNavBarHeight()+'px'}"></view>
		<!-- 个人信息部分 -->
		<view class="userInfo">
			<image class="avatar" src="/static/xxmLogo.png" mode="aspectFill"></image>
			<view class="ip">{{userInfo.IP}}</view>
			<view class="address">来自于:{{userInfo.address?.country}}</view>
		</view>

		<!-- 列表栏部分1 -->
		<view class="section">
			<view class="list">
				<navigator url="/pages/classlist/classlist?name=我的下载&type=download">
					<view class="row">
						<view class="left">
							<uni-icons type="download-filled" size="20" color="#28b389"></uni-icons>
							<view class="text">我的下载</view>
						</view>
						<view class="right">
							<view class="text">{{userInfo.downloadSize}}</view>
							<uni-icons type="right" size="15" color="#aaa"></uni-icons>
						</view>
					</view>
				</navigator>
				<navigator url="/pages/classlist/classlist?name=我的评分&type=score">
					<view class="row">
						<view class="left">
							<uni-icons type="star-filled" size="20" color="#28b389"></uni-icons>
							<view class="text">我的评分</view>
						</view>
						<view class="right">
							<view class="text">{{userInfo.scoreSize}}</view>
							<uni-icons type="right" size="15" color="#aaa"></uni-icons>
						</view>
					</view>
				</navigator>
				<view class="row">
					<view class="left">
						<uni-icons type="chatboxes-filled" size="20" color="#28b389"></uni-icons>
						<view class="text">联系客服</view>
					</view>
					<view class="right">
						<view class="text"></view>
						<uni-icons type="right" size="15" color="#aaa"></uni-icons>
					</view>
					<!-- 条件编译 -->
					<!-- #ifdef MP -->
					<button open-type="contact">联系客服</button>
					<!-- #endif -->
					<!-- #ifndef MP -->
					<button @click="clickContact">拨打电话</button>
					<!-- #endif -->
				</view>
			</view>
		</view>



		<!-- 列表栏部分2 -->
		<view class="section">
			<view class="list">
			<navigator url="/pages/notice/detail?id=653507c6466d417a3718e94b">
				<view class="row">
					<view class="left">
						<uni-icons type="notification-filled" size="20" color="#28b389"></uni-icons>
						<view class="text">订阅更新</view>
					</view>
					<view class="right">
						<view class="text"></view>
						<uni-icons type="right" size="15" color="#aaa"></uni-icons>
					</view>
				</view>
			</navigator>
			<navigator url="/pages/notice/detail?id=6536358ce0ec19c8d67fbe82">
				<view class="row">
					<view class="left">
						<uni-icons type="flag-filled" size="20" color="#28b389"></uni-icons>
						<view class="text">常见问题</view>
					</view>
					<view class="right">
						<view class="text"></view>
						<uni-icons type="right" size="15" color="#aaa"></uni-icons>
					</view>
				</view>
			</navigator>
			</view>
		</view>

	</view>
	<view class="loadingLayout" v-else>
		<view :style="{height: getNavBarHeight()+'px'}"></view>
		<uni-load-more status="loading"></uni-load-more>
	</view>
</template>

<script setup>
import {getNavBarHeight} from '@/utils/system.js'
import {onLoad} from "@dcloudio/uni-app"
import {onMounted,ref} from 'vue'
import {apiUserInfo} from '@/api/apis.js'
const clickContact = () => {
	uni.makePhoneCall({
		phoneNumber: '13672360460aaaa', //仅为示例
	});
}
// 存储用户信息
const userInfo = ref()
const getUserInfo = () =>{
	apiUserInfo().then(res=>{
		userInfo.value = res.data
		console.log(userInfo.value);
	})
}
onMounted(()=>{
	getUserInfo()
})
</script>

<style lang="scss" scoped>
@import url('@/common/style/common-style.scss');
	.userLayout {
		.userInfo {
			display: flex;
			align-items: center;
			justify-content: center;
			flex-direction: column;
			padding: 50rpx 0;

			.avatar {
				height: 160rpx;
				width: 160rpx;
				border-radius: 50%;
			}

			.ip {
				font-size: 44rpx;
				color: #333;
				padding: 20rpx 0 5rpx;
			}

			.address {
				font-size: 28rpx;
				color: #ABABAB;
			}
		}

		.section {
			width: 690rpx;
			margin: 50rpx auto;
			border: 1px solid #eee;
			border-radius: 10rpx;
			box-shadow: 0 0 30rpx rgba(0, 0, 0, 0.05);

			.row {
				height: 100rpx;
				display: flex;
				justify-content: space-between;
				padding: 0 30rpx;
				border-bottom: 1px solid #eee;
				position: relative;

				button {
					position: absolute;
					top: 0;
					left: 0;
					height: 100%;
					width: 100%;
					opacity: 0;
				}

				.left {
					display: flex;
					align-items: center;
					justify-content: center;

					.text {
						padding-left: 20rpx;
						color: #666;
					}
				}

				.right {
					display: flex;
					align-items: center;
					justify-content: center;

					.text {
						font-size: 28rpx;
						margin-right: 6rpx;
						color: #aaa;
					}
				}
			}

			.row:last-child {
				border-bottom: 0;
			}
		}
	}
</style>